const attackMonsterImg4 = new Image();
attackMonsterImg4.src = attackMonsterImg1.src;
const attackMonsterImgs = [attackMonsterImg0, attackMonsterImg1, attackMonsterImg2, attackMonsterImg3, attackMonsterImg4];

const diedMonsterImg0 = new Image();
diedMonsterImg0.src = 'images/monsters/Golem_1/0_Golem_Dying_000.png';
const diedMonsterImg1 = new Image();
diedMonsterImg1.src = 'images/monsters/Golem_1/0_Golem_Dying_004.png';
const diedMonsterImg2 = new Image();
