const diedMonsterImgs = [diedMonsterImg0, diedMonsterImg1, diedMonsterImg2, diedMonsterImg3, diedMonsterImg4];

function heroStay() {
  stayHeroImgs.forEach((value,index) => heroStatus.imgs[index] = value);
}
function heroRun() {
  runHeroImgs.forEach((value,index) => heroStatus.imgs[index] = value);
}
function heroAttack() {
  attackHeroImgs.forEach((value,index) => heroStatus.imgs[index] = value);
}
function heroDied() {
  diedHeroImgs.forEach((value,index) => heroStatus.imgs[index] = value);
}
function monsterStay() {
  stayMonsterImgs.forEach((value,index) => monsterStatus.imgs[index] = value);
}
function monsterRun() {
  runMonsterImgs.forEach((value,index) => monsterStatus.imgs[index] = value);
}
function monsterAttack() {
  attackMonsterImgs.forEach((value,index) => monsterStatus.imgs[index] = value);
}
function monsterDied() {
  diedMonsterImgs.forEach((value,index) => monsterStatus.imgs[index] = value);
}

function drawStricts() {
  const width = leftStrip.clientWidth / gameInfo.countBlockAtStrips;
  for (let i = 0; i < gameInfo.countBlockAtStrips; i++) {
    const block = document.createElement('div');
    block.style.width = width + 'px';
    block.style.height = '100%';
    block.style.float = 'left';
    leftStrip.appendChild(block);
  }
  for (let i = 0; i < gameInfo.countBlockAtStrips; i++) {
    const block = document.createElement('div');
    block.style.width = width + 'px';
    block.style.height = '100%';
    block.style.float = 'left';
    rightStrip.appendChild(block);
  }
}

function loseHealth(status) {
  if (status === heroStatus) {
    const variableBlock = heroStatus.health - 1;
    const block = leftStrip.childNodes[variableBlock];
    block.style.background = 'red';
    heroStatus.health -= 1;
  } else {
    const variableBlock = monsterStatus.health - 1;
    const block = rightStrip.childNodes[variableBlock];
    block.style.background = 'red';
    monsterStatus.health -= 1;
  }
}

function main() {
  button.addEventListener('click', () => {
    const userIsWin = false; //TODO: function
    if (userIsWin) {
      heroRun();
      gameInfo.gameStatus = 'heroRun';
    } else {
      monsterRun();
      gameInfo.gameStatus = 'monsterRun';
    }
  });

  let heroPositionX = heroStatus.positionX;
  let monsterPositionX = monsterStatus.positionX;

  function draw() {
    switch (gameInfo.gameStatus) {
      case 'heroRun': {
        heroPositionX += gameInfo.characterSpeedInfo;
        if (heroPositionX + gameInfo.gaps.whenHeroRun > monsterPositionX) {
          gameInfo.gameStatus = 'heroAttack';
          heroAttack();
        }
        break;
      }
      case 'heroAttack': {
        if (gameInfo.numberOfStayImgs === 0) {
          gameInfo.countOfAttack += 1;
        }
        if (gameInfo.countOfAttack === 2) {
          gameInfo.countOfAttack = 0;
          loseHealth(monsterStatus);
          if (monsterStatus.health === 0) {
            monsterDied();
            monsterPositionX = monsterStatus.positionX - 60;
            monsterStatus.positionY -= 20;
          }
          gameInfo.gameStatus = 'heroRunBack';
          heroRun();
        }
        break;
      }
      case 'heroRunBack': {
        heroPositionX += -1 * gameInfo.characterSpeedInfoBack;
        if (heroPositionX <= heroStatus.positionX + gameInfo.gaps.whenHeroRunBack) {
          heroPositionX = heroStatus.positionX;
          heroStay();
        }
        break;
      }
      case 'monsterRun': {
        monsterPositionX -= gameInfo.characterSpeedInfo;
        if (monsterPositionX - gameInfo.gaps.whenMonsterRun < heroPositionX) {
          gameInfo.gameStatus = 'monsterAttack';
          monsterAttack();
        }
        break;
      }
      case 'monsterAttack': {
        if (gameInfo.numberOfStayImgs === 0) {
          gameInfo.countOfAttack += 1;
        }
        if (gameInfo.countOfAttack === 2) {
          gameInfo.countOfAttack = 0;
          loseHealth(heroStatus);
          const health = heroStatus.health;
          if (health === 0) {
            heroStatus.width += 120;
            heroPositionX -= 50;
            heroDied();
          }
          gameInfo.gameStatus = 'monsterRunBack';
          monsterRun();
        }
        break;
      }
      case 'monsterRunBack': {
        monsterPositionX += gameInfo.characterSpeedInfoBack;
        if (monsterPositionX >= monsterStatus.positionX - gameInfo.gaps.whenMonsterRunBack) {
          monsterPositionX = monsterStatus.positionX;
          monsterStay();
        }
        break;
      }
    }

    ctx.clearRect(0, 0, canvas.width, canvas.height);
    const statusImgsNumber = Math.floor(gameInfo.numberOfStayImgs / gameInfo.duration);
    ctx.drawImage(
      heroStatus.imgs[statusImgsNumber],
      heroPositionX,
      heroStatus.positionY,
      heroStatus.width,
      heroStatus.height
    );
    ctx.drawImage(
      monsterStatus.imgs[statusImgsNumber],
      monsterPositionX,
      monsterStatus.positionY,
      monsterStatus.width,
      monsterStatus.height
    );
    const numberOfLastImg = heroStatus.imgs.length - 1;
    if (statusImgsNumber !== numberOfLastImg) {
      gameInfo.numberOfStayImgs += 1;
    } else {
      if (monsterStatus.health === 0 || heroStatus.health === 0) {
        //TODO: add gameOver function
        return;
      }
      gameInfo.numberOfStayImgs = 0;
    }
    window.requestAnimationFrame(draw);
  }
  draw();
}
window.onload = () => {
  drawStricts();
  heroStay();
  monsterStay();
  main();
};
